import React, { Component } from 'react';
import Item from './Item';
import PropTypes from 'prop-types'
export class MainPage extends Component {
  static defaultProps = {
    url_i: ''
}
static propTypes = {
    url_i: PropTypes.string
}
    constructor(props){
        super(props);
        this.state = {
          results : []
        }
      }
async componentDidMount(){
        let url = `${this.props.url_i}`
        let data =await fetch(url);
        let parsedData = await data.json();
        this.setState({results: parsedData.results})
        
    }  
  render() {
    return <div className="container my-3">
        <div className="row">
        {this.state.results.map((element)=>{
            return <div className="col-md-4">
            <Item key={element.poster_path} title={element.title} decri={element.overview} imageUrl={`https://image.tmdb.org/t/p/w500${element.backdrop_path}`}/>
        </div>
        })}
        </div>
    </div>;
  }
}

export default MainPage;
