import { getByTitle } from '@testing-library/react';
import React, { Component } from 'react';

export class Item extends Component {

  render() {
     let {title,decri,imageUrl} = this.props;
    return <div>
        <div className="card my-3" style={{width: "18rem"}}>
  <img src={imageUrl} className="card-img-top" alt="..."/>
  <div className="card-body">
    <h5 className="card-title" style={{height: "5rem"}}>{title}</h5>
    <p className="card-text" style={{height: "7.5rem",overflow: "hidden"}}>{decri}</p>
    <a href={`https://www.youtube.com/results?search_query=${title} trailer`} target="_blank" className="btn btn-primary">Get Trailer</a>
  </div>
</div>
    </div>;
  }
}

export default Item;
