import PropTypes from 'prop-types';
import React, { Component } from 'react';
import {
  Link
} from "react-router-dom";
export class navbar extends Component {
  static propTypes = {};

  render() {
    return <div>
        <nav className="navbar navbar-expand-lg navbar-light bg-light">
  <div className="container-fluid">
    <a className="navbar-brand" href="/">Movie Bazzar</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/">Home</Link>
        </li>
        <li className="nav-item"><Link className="nav-link" to="/Action" >Action</Link></li>
        <li className="nav-item"><Link className="nav-link" to="/Adventure">Adventure</Link></li>
        <li className="nav-item"><Link className="nav-link" to="/Animation">Animation</Link></li>
        <li className="nav-item"><Link className="nav-link" to="/Comedy">Comedy</Link></li>
        <li className="nav-item"><Link className="nav-link" to="/Crime">Crime</Link></li>
        <li className="nav-item"><Link className="nav-link" to="/Horror">Horror</Link></li>
      </ul>
      <form className="d-flex" id="form">
        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search"/>
        <button className="btn btn-outline-success" type="submit" id="searcher">Search</button>
      </form>
    </div>
  </div>
</nav>
    </div>;
  }
}

export default navbar;
